.reposAndFormContainer {
  width: 350px;
  padding: 5px;

  > h3 {
    margin-bottom: 10px;
    font-size: 26px;
  }

  .forBorderWrap {
    border: 1px solid #282c34;

    .formContainer {
      h4 {
        margin-bottom: 6px;
        margin-top: 10px;
      }

    }

    .repo {
      padding-left: 5px;
      padding-right: 5px;
      border-top: 1px solid #282c34;

      a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px;
        color: #282c34;

        .repoStats {
          width: 60px;
        }

        .repos {

        }

      }
    }
  }


}